.white-text-box{
    background: #ffffff;
    position: absolute;
    font-size: 18px;
    border-radius: 70px;
    padding: 25px;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}

.centerText{
    text-align: center;
    font-weight: bold;
    margin-bottom: 0;
    margin-top: 0;
}